.app-wrapper {
  margin: 0px auto;
  display: grid;
  width: 1280px;
  grid-template-areas:
  "h   h"
  "n   c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
}

.app-wrapper-content{
  grid-area: c;
  background-color: beige;
}
.content img {
  width: 600px;
}
.item {
  color: green;
}



